@tailwind base;
@tailwind components;
@tailwind utilities;

#own {
  border-radius: 180px;
  width: 28%;
  max-width: 100%;
  height: auto;
  filter: grayscale(100%);
  transition: ease-in-out 0.5s;
}

#own:hover {
  width: 34%;
  border-radius: 20px;
  filter: grayscale(0);
}

#container {
  width: 100%;
}

[data-theme="dracula"] .test {
  background-color: #f0f4fc;
}

[data-theme="light"] .txt {
  color: white;
}

[data-theme="dracula"] .txt {
  color: #363434;
}

.txt {
  font-family: "Protest Strike", sans-serif;
  font-weight: 400;
  font-style: normal;
}

[data-theme="light"] .txt1 {
  color: white;
}

[data-theme="dracula"] .txt1 {
  color: #363434;
}

[data-theme="light"] .ig {
  color: white;
  font-size: 31px;
  transition: color .3s ease-in-out, transform .3s ease-in-out;
}

[data-theme="light"] .ig:hover {
  color: #fc8c03;
  transform: scale(1.3);
}

[data-theme="dracula"] .ig {
  color: black;
  font-size: 31px;
  transition: color .3s ease-in-out, transform .3s ease-in-out;
}

[data-theme="dracula"] .ig:hover {
  color: #d67702;
  transform: scale(1.3);
}

